.display{
    background-color: lightgrey;
    color: #212121;
    opacity: 0.75;
    font-weight: bold;
    padding: 5px 20px;
    border-radius: 5px;
    box-shadow: 0 6px 12px rgba(0,0,0,0.2);
    margin-top: 10px;
    cursor: pointer;
}

.display input {
    border: 0;
    background-color: transparent;
    text-align: center;
    font-size: 1em;
    display: inline-block;
    font-weight: 520;
    min-width: 200px;
    cursor: pointer;
}

.display input:focus {
    outline: none;
}

.display:hover {
    box-shadow: 0 6px 12px rgba(0,0,0,.2);
    transform: scale(1.05);
    transition: 0.3s;
}